/*----------------------------------ajax----------------------------*/
export function ajax(options) {
  if ($.cookie("_token_")) {
    options.headers = {
      "Authorization": "Bearer " + $.cookie("_token_")
    };
  } else {
    options.headers = {};
  }
  let flag = '?';
  if (options.url.indexOf('?') > -1) {
    flag = '&'
  }
  $.ajax({
    type: options.type || "post",
    url: "/" + options.url + (options.cache ? '' : flag + "random=" + Math.random()),
    dataType: options.dataType || "json",
    beforeSend: function(request) {
      if (!options.isLogin) {
        request.setRequestHeader("Authorization", "Bearer " + $.cookie("_token_"));
      }
    },
    contentType: options.content_type || "application/json",
    timeout: options.timeout || 10000,
    async: (options.async != undefined) ? options.async : true,
    data: options.data || {},
    headers: options.headers,
    complete: function() {
      if (!options.noHideLoading) {
        layer.closeAll('loading');
      }
    },
    success: function(result) {
      if (!options.noHideLoading) {
        layer.closeAll('loading');
      }
      if (result) {
        if (result.error_code || result.error_code == "") {
          if (options.failure) {
            options.failure(result)
          } else {
            openLayer(result.error_info);
          }
        } else {
          _callSuccess(options.callback, result);
        }
      } else {
        _callSuccess(options.callback, result);
      }
    },
    error: function(XMLHttpRequest, textStatus, errorThrown) {
      if (!options.noHideLoading) {
        layer.closeAll('loading');
      }
      if (options.hideLayer) {
        layer.closeAll();
      }
      if (XMLHttpRequest.status == '500' || XMLHttpRequest.status == '401') {
        var obj = JSON.parse(XMLHttpRequest.responseText);
        //XMLHttpRequest.responseText

        if (obj.code == '10000') {
          // layer.open({
          //   type: 1,
          //   title: '',
          //   area: ['480px', ''],
          //   shadeClose: true, //
          //   content: '<div>' +
          //     '<h3 class="health-score"></h3>' +
          //     '</div>'
          // });
           openLayer('');
        } else if (obj.code == '20401' || obj.code == "10007") {
          tip_msg("", null, function() {
            logout();
          });
        } else if (obj.error == "invalid_token") {
          logout();
        } else {
          _callError(options.failure, obj);
        }
      } else {
        layer.close();
        layer.open({
          type: 1,
          title: '',
          area: ['480px', ''],
          shadeClose: true, //
          content: '<div>' +
            '<h3 class="health-score"></h3>' +
            '</div>'
        });
        _callError(options.failure, obj);
      }

    }
  })
}

/**
 *  
 * @param callback(Function) 
 * @param result 
 */
function _callSuccess(callback, result) {
  if (typeof(callback) == 'function') {
    callback(result);
  }
}

/**
 * 
 * @param failure(Function) 
 * @param errorMessage(String) 
 *
 */
function _callError(failure, errorMessage) {
  if (typeof(failure) == 'function') {
    failure(errorMessage);
  } else {
    openLayer(errorMessage.message);
  }
}


var z;
export function ajaxAsync(options) {
  options.hideLayer = options.hideLayer == undefined ? true : options.hideLayer;
  if ($.cookie("_token_")) {
    options.headers = {
      "Authorization": "Bearer " + $.cookie("_token_")
    };
  } else {
    options.headers = {};
  }

  return new Promise((resolve, reject) => {
    //layer.load(2,{offset: ['50%','50%']});
      let flag = '?';
    if (options.url.indexOf('?') > -1) {
      flag = '&'
    }
    $.ajax({
      type: options.type || "post",
      url: "/" + options.url + (options.cache ? '' : flag + "random=" + Math.random()),
      cache: options.cache || false,
      dataType: options.dataType || "json",
      beforeSend: function(request) {
        // layer.closeAll();
        if (!options.isLogin) {
          request.setRequestHeader("Authorization", "Bearer " + $.cookie("_token_"));
        }
      },
      contentType: options.content_type || "application/json",
      timeout: options.timeout || 10000,
      async: (options.async != undefined) ? options.async : true,
      data: options.data || {},
      headers: options.headers,
      complete: function() {
        if (!options.noHideLoading) {
          layer.closeAll('loading');
        }
      },
      success: function(result) {
        if (!options.noHideLoading) {
          layer.closeAll('loading');
        }
        if (result) {
          if (result.error_code || result.error_code == "") {
            reject(result);
          } else {
            resolve(result);
          }
        } else {
          resolve(result);
        }
      },
      error: function(XMLHttpRequest, textStatus, errorThrown) {
        if (!options.noHideLoading) {
          layer.closeAll('loading');
        }
        if (options.hideLayer) {
          layer.closeAll();
        }
         if (options.is_failure === 'first') {
           reject(XMLHttpRequest);
           return;
         }
        if (XMLHttpRequest.status == '500' || XMLHttpRequest.status == '401') {
          var obj = JSON.parse(XMLHttpRequest.responseText);
          //XMLHttpRequest.responseText

          if (obj.code == '10000') {
            // layer.open({
            //   type: 1,
            //   title: '',
            //   area: ['480px', ''],
            //   shadeClose: true, //
            //   content: '<div>' +
            //     '<h3 class="health-score"></h3>' +
            //     '</div>'
            // });
            openLayer('');
          } else if (obj.code == '20401' || obj.code == "10007") {
            tip_msg("", null, function() {
              logout();
            });
          } else if (obj.error == "invalid_token") {
            logout();
          } else {
            if (!options.showFailure) {
              openLayer(obj.message);
            }
          }
          //reject(obj);
        } else {
          try {
            layer.close(z);
          } catch (e) {}
          // z = layer.open({
          //   type: 1,
          //   title: '',
          //   area: ['480px', ''],
          //   shadeClose: true, //
          //   content: '<div>' +
          //     '<h3 class="health-score">!</h3>' +
          //     '</div>'
          // });
            openLayer('');
          //reject(obj);
        }
        reject(XMLHttpRequest);
      }
    })
  })
}


